#punctuation-deep-check-loader {
  &.loading-box {
    position:fixed;
    left:0;
    right:0;
    bottom:0;
    top:0;
    z-index:100;

    .background {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(255,255,255,0.6);
    }

    .pbox {
      position: absolute;
      background: #fff;
      opacity: 1;
      padding: 80px 15px 15px 15px;
      border-radius: 10%;
      top: 35%;
      left: 50%;
      transform: translate(-50%,-50%);
      -webkit-transform: translate(-50%,-50%);
      -moz-transform: translate(-50%,-50%);
      color: #333;
    }

    .pbox {
      img {
        position: absolute;
        top: 15px;
        left: 50%;
        width: 50px;
        height: 50px;
        margin-left: -25px;
      }
    }
  }
}



